import { createStore } from 'vuex'

export default createStore({
  state: {
    amountModalIsActive: false,
    authed: false,
    customerId: "",
    userName: "",
    allPayments: [],
    allCustomers: [],
    findCustomer: [],

  },
  getters: {
    getAllPayment(state) {
      return state.allPayments;
    },
    getCustomerId(state) {
      return state.customerId;
    },
    getFindCustomer(state) {
      return state.findCustomer;
    },
    getAllCustomers(state) {
      return state.allCustomers;
    }
  },
  mutations: {
    updateAmountModal(state, payload) {
      state.amountModalIsActive = payload
    },
    setAuthed(state, payload) {
      state.authed = payload
    },
    setUserName(state, payload) {
      state.userName = payload
    },
    setAllPayments(state, payload) {
      state.allPayments = payload
    },
    setFindCustomer(state, payload) {
      let fc = state;
      fc = [];
      fc.push(payload);
      state.findCustomer = fc;
    },
    setAllCustomers(state, payload) {
      state.allCustomers = payload;
    },
    setCustomerId(state, payload) {
      state.customerId = payload;
    },
  },
  actions: {
    changeAmountModal({ commit }, data) {
      commit("updateAmountModal", data);
    },
    updateAuthed({ commit }, payload) {
      commit('setAuthed', payload)
    },
    updateUserName({ commit }, payload) {
      commit('setUserName', payload)
    },
    updateAllPayments({ commit }, payload) {
      commit('setAllPayments', payload)
    },
    updateFindCustomer({ commit }, payload) {
      commit('setFindCustomer', payload)
    },
    updateAllCustomer({ commit }, payload) {
      commit('setAllCustomers', payload)
    },
    updateCustomerId({ commit }, payload) {
      commit('setCustomerId', payload)
    },
  },
  modules: {
  }
})
