<template>
  <div
    class="tab-pane fade show active"
    id="nav-clients"
    role="tabpanel"
    aria-labelledby="nav-clients-tab"
  >
    <div class="container">
      <div class="row justify-content-between">
        <div class="col-sm-12">
          <DxDataGrid
            :data-source="allCustomers"
            :key="customRerender"
            :remote-operations="true"
            :show-borders="true"
          >
            <DxFilterRow :visible="true" />
            <DxColumn data-field="id" data-type="number" :width="30" />
            <DxColumn data-field="dateTime" data-type="string" :width="150" />
            <DxColumn data-field="login" data-type="string" :width="130" />
            <DxColumn data-field="status" data-type="string" :width="70" />
            <DxColumn data-field="deviceId" data-type="string" :width="100" />
            <DxColumn data-field="deviceOS" data-type="string" :width="100" />
            <DxColumn
              data-field="deviceModel"
              data-type="string"
              :width="100"
            />
            <DxColumn data-field="ipAddress" data-type="string" :width="100" />
            <DxColumn data-field="balance" data-type="number" :width="100" />
            <DxColumn
              data-field="lastActiveTime"
              data-type="string"
              :width="100"
            />
            <DxColumn data-field="appVersion" data-type="string" :width="100" />
            <DxColumn data-field="lang" data-type="string" :width="100" />
            <DxColumn data-field="cusName" data-type="string" :width="120" />
            <DxColumn type="buttons" :width="110">
              <DxButton
                text="Ödəniş Et"
                css-class="btn btn-primary text-white"
                @click="openAmountModal"
              />
            </DxColumn>

            <DxPaging :page-size="20" />

            <DxScrolling column-rendering-mode="virtual" />
          </DxDataGrid>
        </div>
      </div>
    </div>
  </div>
</template>


<script>
import {
  DxDataGrid,
  DxColumn,
  DxPaging,
  DxButton,
  DxScrolling,
  DxFilterRow,
} from "devextreme-vue/data-grid";
import apiService from "@/APIService/APIService";
import { mapActions, mapState } from "vuex";
import { mapGetters } from "vuex";
export default {
  components: {
    DxDataGrid,
    DxPaging,
    DxColumn,
    DxButton,
    DxScrolling,
    DxFilterRow,
  },
  data() {
    return {
      customRerender: 0,
      customerNumber: "",
    };
  },

  computed: {
    ...mapState(["amountModalIsActive", "allCustomers"]),
    ...mapGetters(["getAllCustomers"]),
  },
  methods: {
    ...mapActions(["changeAmountModal"]),
    openAmountModal(e) {
      this.changeAmountModal(true);
      this.$store.dispatch("updateCustomerId", e.row.data.id);
    },
  },
  mounted() {
    const data = { mobilePhone: this.customerNumber };

    apiService.getAllCustomers(data).then((response) => {
      if (response.data.success == true) {
        this.$store.dispatch("updateAllCustomer", response.data.data);
        this.customRerender++;
      }
    });
  },
};
</script>


<style lang="scss" scoped>
.row {
  margin-top: 5rem;
}
@media only screen and (max-width: 992px) {
  .row {
    flex-direction: column-reverse;
  }
}
label {
  font-size: 1.3rem;
}
input {
  height: 4rem !important;
  font-size: 1.4rem;
  display: flex;
  align-items: center;
  justify-content: center;
  border: 0.1rem solid #ccc;
}
button {
  width: 8rem;
  height: 4rem;
}
</style>