<template>
  <nav class="navbar">
    <div class="container">
      <div class="navbar-user">
        <div class="navbar-user__details">
          Username: {{userName}} |
          <button class="btn btn-danger" @click="LogOut">Logout</button>
        </div>
        <div class="navbar-user__currentdate">Tarix: {{this.date}}</div>
      </div>
      <div class="navbar-logo">
        <router-link to="/">
          <img src="@/assets/logo.png" alt="">
        </router-link>
      </div>
    </div>
  </nav>
</template>

<script>
import { mapState } from "vuex";
import moment from "moment";
export default {
  data() {
    return {
      date: new Date(),
    }
  },
  methods: {
    LogOut() {
      localStorage.setItem("authed", false);
      localStorage.removeItem("JWT");
      localStorage.removeItem("userName");
      location.reload(true)
    },
  },
  computed: {
    ...mapState(["userName"]),
  },
  mounted(){
    this.date = moment(this.date).format("DD.MM.YYYY")
  }
};
</script>

<style lang="scss" scoped>
@import "@/assets/variable.scss";
.navbar {
  padding: 1rem 0;
  box-shadow: 0px 0px 8px 2px rgba(82, 82, 82, 0.43);
  -webkit-box-shadow: 0px 0px 8px 2px rgba(82, 82, 82, 0.43);
  -moz-box-shadow: 0px 0px 8px 2px rgba(82, 82, 82, 0.43);

  .navbar-user {
    &__details {
      font-size: 1.6rem;
      margin-bottom: 1rem;
    }
    &__currentdate {
      font-size: 1.3rem;
    }
  }
  &-logo {
    width: 120px;
    img {
      width: 100%;
    }
  }
  .container {
    display: flex;
    align-items: center;
    justify-content: space-between;
  }
}
</style>