<template>
  <transition name="fade">
    <div class="amountModal" id="amountModal" v-if="amountModalIsActive">
      <div class="amountModal__content">
        <form action="" class="form">
          <div class="row">
            <label for="inputEmail3" class="col-sm-12 col-md-2 col-form-label"
              >Məbləğ</label
            >
            <div class="col-sm-12 col-md-10">
              <input
                type="number"
                class="form-control"
                id="inputEmail3"
                v-model="amount"
              />
            </div>
          </div>
          <div class="button-group">
            <button
              type="submit"
              class="btn btn-primary btn-lg"
              @click.prevent="cashPayment"
            >
              Ok
            </button>
            <button
              type="button"
              class="btn btn-secondary btn-lg"
              @click.prevent="amountModalClose"
            >
              Imtina
            </button>
          </div>
        </form>
      </div>
    </div>
  </transition>
</template>

<script>
import { mapActions, mapState } from "vuex";
import apiService from "@/APIService/APIService";
export default {
  data() {
    return {
      amount: null,
    };
  },

  methods: {
    ...mapActions(["changeAmountModal"]),
    amountModalClose() {
      this.changeAmountModal(false);
      this.amount = "";
    },
    cashPayment() {
      const data = {
        customerId: this.customerId,
        amount: this.amount,
      };

      apiService.cashPayment(data).then((response) => {
        if (response.data.success == true) {
          this.changeAmountModal(false);
          this.amount = "";
        }
      });
    },
  },
  computed: {
    ...mapState(["amountModalIsActive", "customerId"]),
  },
};
</script>

<style lang="scss" scoped>
.amountModal {
  width: 100%;
  height: 100%;
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  display: flex;
  align-items: center;
  justify-content: center;
  background-color: rgba(0, 0, 0, 0.5);
  z-index: 9999999;
  &__content {
    width: 50rem;
    min-width: 30rem;
    height: 20rem;
    background-color: #ffffff;
    .form {
      display: flex;
      flex-direction: column;
      align-items: center;
      justify-content: center;
      padding: 5rem;
      .row {
        width: 100%;
        .form-control {
          height: 3rem;
        }
      }
      .button-group {
        column-gap: 4rem;
        margin-top: 3rem;
        display: flex;
        align-items: center;
        justify-content: space-between;
        button {
          width: 12rem;
        }
      }
    }
  }
}
.amountModal.activemodal {
  display: flex;
}

@media only screen and (max-width: 450px) {
  .amountModal .amountModal__content .form {
    padding: 2rem;
  }
}
.fade-enter-active,
.fade-leave-active {
  transition: opacity 0.3s ease;
}
.fade-enter-from,
.fade-leave-to {
  opacity: 0;
}
</style> 

