<template>
  <div
    class="tab-pane fade my-5"
    id="nav-pays"
    role="tabpanel"
    aria-labelledby="nav-pays-tab"
  >
    <div class="dx-field row">
      <div class="col-sm-12 col-md-4 col-lg-4">
        <div class="dx-field-value">
          <DxDateBox
            :show-clear-button="true"
            :use-mask-behavior="true"
            :value="date"
            placeholder="tarixdən"
            display-format="d.MM.yyyy"
            type="date"
            v-model="dateFrom"
          />
        </div>
      </div>
      <div class="col-sm-12 col-md-4 col-lg-4">
        <div class="dx-field-value">
          <DxDateBox
            :show-clear-button="true"
            :use-mask-behavior="true"
            :value="date"
            placeholder="tarixə"
            display-format="d.MM.yyyy"
            type="date"
            v-model="dateTo"
          />
        </div>
      </div>
      <div class="col-sm-12 col-md-1">
        <button class="btn btn-secondary w-100" @click="getDateValue">
          Ok
        </button>
      </div>
    </div>
    <DxDataGrid
      :data-source="getAllPayment"
      :show-borders="true"
      @exporting="onExporting"
    >
      <DxSearchPanel :visible="true" />
      <DxExport :enabled="true" :allow-export-selected-data="true" />
      <DxColumn data-field="paymentId" data-type="number" :width="100" />
      <DxColumn data-field="amount" data-type="string" :width="100" />
      <DxColumn data-field="bonusAmount" data-type="string" :width="150" />
      <DxColumn data-field="customerId" data-type="number" :width="100" />
      <DxColumn data-field="dateTime" data-type="string" :width="200" />
      <DxColumn data-field="intRef" data-type="string" :width="100" />
      <DxColumn data-field="paymentType" data-type="string" :width="150" />
      <DxColumn data-field="rrn" data-type="string" :width="100" />
      <DxColumn data-field="status" data-type="number" :width="100" />
      <DxColumn data-field="userId" data-type="string" :width="100" />

      <DxPaging :page-size="20" />
      <DxScrolling column-rendering-mode="virtual" />
    </DxDataGrid>
  </div>
</template>
<script>
import { Workbook } from "exceljs";
import { saveAs } from "file-saver-es/dist/FileSaver.min";
import DxDateBox from "devextreme-vue/date-box";
import { exportDataGrid } from "devextreme/excel_exporter";
import moment from "moment";
import apiService from "@/APIService/APIService";
import { mapGetters } from "vuex";
import {
  DxDataGrid,
  DxColumn,
  DxPaging,
  DxExport,
  DxSearchPanel,
  DxScrolling,
} from "devextreme-vue/data-grid";
export default {
  components: {
    DxDataGrid,
    DxPaging,
    DxColumn,
    DxExport,
    DxSearchPanel,
    DxDateBox,
    DxScrolling,
  },
  data() {
    return {
      date: new Date(2022, 5, 12),
      dateFrom: "",
      dateTo: "",
    };
  },
  computed: {
    ...mapGetters(["getAllPayment"]),
  },
  methods: {
    onExporting(e) {
      const workbook = new Workbook();
      const worksheet = workbook.addWorksheet("Employees");

      exportDataGrid({
        component: e.component,
        worksheet,
        autoFilterEnabled: true,
      }).then(() => {
        workbook.xlsx.writeBuffer().then((buffer) => {
          saveAs(
            new Blob([buffer], { type: "application/octet-stream" }),
            "DataGrid.xlsx"
          );
        });
      });
      e.cancel = true;
    },
    getDateValue() {
      const data = {
        dateFrom: moment(this.dateFrom).format("YYYYMMDD"),
        dateTo: moment(this.dateTo).format("YYYYMMDD"),
      };
      apiService.getAllPayments(data).then((response) => {
        if (response.data.success == true) {
          this.$store.dispatch("updateAllPayments", response.data.data);
        }
      });
    },
  },
};
</script>


<style lang="scss" scoped>
.tab-pane {
  margin-top: 5rem;
}

.dx-field.row {
  .dx-field-value {
    float: left;
  }
  button {
    height: 3.6rem;
  }
}
.dx-field-value:not(.dx-switch):not(.dx-checkbox):not(.dx-button) {
  width: 100%;
}
.mb_number {
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: flex-end;
  column-gap: 1rem;
  span {
    font-size: 1.3rem;
  }
  div {
    width: 15rem;
    height: 3rem;
    font-size: 1.4rem;
    display: flex;
    align-items: center;
    justify-content: center;
    border: 0.1rem solid #ccc;
  }
}
</style>