import axios from 'axios';
import { BASE_URL } from './Variables';

export const PAYMENT = {
    cashPayment(data) {
        const url = `${BASE_URL}/app/payment/cashPayment`;
        return axios.post(url, data);
    },
    getAllPayments(data) {
        const url = `${BASE_URL}/app/payment/getAllPayments`;
        return axios.post(url, data);
    },
}