<template>
  <div
    class="tab-pane fade show"
    id="nav-client"
    role="tabpanel"
    aria-labelledby="nav-client-tab"
  >
    <div class="container">
      <div class="row justify-content-between flex-column">
        <div class="col-sm-12 col-md-12 col-lg-6 mb-5">
          <label>Mobil Nömrə üzrə axtarış</label>
          <div class="input-group align-items-center">
            <input
              type="text"
              class="form-control my-3"
              aria-describedby="button-addon2"
              v-model="customerNumber"
            />
            <button
              class="btn btn-outline-secondary"
              type="button"
              id="button-addon2"
              @click.prevent="findCustomerFn"
            >
              Axtar
            </button>
          </div>
        </div>
        <div
          class="col-sm-12 col-md-6 col-lg-4 col-xl-3"
          v-for="c in findCustomer"
          :key="c.id"
        >
          <div class="card bg-primary border-0 p-4">
            <p class="heading">
              Column <span>Data</span>
            </p>
            <hr>
            <p>
              appVersion: <span>{{ c.appVersion }}</span>
            </p>
            <p>
              balance: <span>{{ c.balance }}</span>
            </p>
            <p>
              cusName: <span>{{ c.cusName }}</span>
            </p>
            <p>
              dateTime: <span>{{ c.dateTime }}</span>
            </p>
            <p>
              deviceId: <span>{{ c.deviceId }}</span>
            </p>
            <p>
              deviceModel: <span>{{ c.deviceModel }}</span>
            </p>
            <p>
              deviceOS: <span>{{ c.deviceOS }}</span>
            </p>
            <p>
              id: <span>{{ c.id }}</span>
            </p>
            <p>
              ipAddress: <span>{{ c.ipAddress }}</span>
            </p>
            <p>
              lang: <span>{{ c.lang }}</span>
            </p>
            <p>
              lastActiveTime: <span>{{ c.lastActiveTime }}</span>
            </p>
            <p>
              login: <span>{{ c.login }}</span>
            </p>
            <p>
              status: <span>{{ c.status }}</span>
            </p>
            <button class="btn btn-light" @click="openAmountModal">
              Ödəniş Et
            </button>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>


<script>
import apiService from "@/APIService/APIService";
import { mapActions, mapState } from "vuex";
import { mapGetters } from "vuex";
export default {
  data() {
    return {
      customRerender: 0,
      customerNumber: "",
    };
  },

  computed: {
    ...mapState(["amountModalIsActive", "findCustomer"]),
    ...mapGetters(["getFindCustomer"]),
  },
  methods: {
    ...mapActions(["changeAmountModal"]),
    openAmountModal() {
      this.changeAmountModal(true);
    },
    findCustomerFn() {
      const data = { mobilePhone: this.customerNumber };

      apiService.findCustomer(data).then((response) => {
        if (response.data.success == true) {
          this.$store.dispatch("updateFindCustomer", response.data.data);
          this.$store.dispatch("updateCustomerId", response.data.data.id);
          this.customRerender++;
        }
      });
    },
  },
};
</script>


<style lang="scss" scoped>
.row {
  margin-top: 5rem;
}
@media only screen and (max-width: 992px) {
  .row {
    flex-direction: column-reverse;
  }
}
label {
  font-size: 1.3rem;
}
input {
  height: 4rem !important;
  font-size: 1.4rem;
  display: flex;
  align-items: center;
  justify-content: center;
  border: 0.1rem solid #ccc;
}
button {
  width: 8rem;
  height: 4rem;
}
.card.bg-primary {
  background-color: #00aff0 !important;

  p {
    color: #ffffff;
    text-transform: capitalize;
    display: flex;
    justify-content: space-between;
    &.heading {
      font-size: 1.7rem;
      font-weight: 700;
      margin-bottom: 0;
    }
  }
  hr {
    height: 0;
    border: 1px solid #ffffff;
  }
  button {
    margin-top: 3rem;
    margin-left: auto;
  }
}
</style>