<template>
  <div class="login">
    <div class="login-card">
      <form action="" class="login-form form">
        <div class="form-group">
          <label for="" class="form-label">Login</label>
          <input type="text" class="form-control" v-model="userLogin" />
        </div>
        <div class="form-group">
          <label for="" class="form-label">Password</label>
          <input type="password" class="form-control" v-model="userPsw" />
        </div>
        <button
          type="submit"
          class="btn btn-primary btn-lg"
          @click.prevent="Login"
        >
          Daxil Ol
        </button>
      </form>
    </div>
  </div>
</template>

<script>
import apiService from "@/APIService/APIService";
import { mapState, mapActions } from "vuex";

export default {
  data() {
    return {
      userLogin: "",
      userPsw: "",
    };
  },
  methods: {
    ...mapActions(["updateAuthed", "updateUserName"]),
    Login() {
      const data = {
        userLogin: this.userLogin,
        userPsw: this.userPsw,
      };

      apiService
        .logIn(data)
        .then((response) => {
          if (response.data.success == true) {
            this.updateAuthed(true);
            localStorage.setItem("authed", true);
            localStorage.setItem("JWT", response.data.data.token);
            localStorage.setItem("userName",  response.data.data.userName)
            this.$router.push({name: "Home"});
            
          }
        })
        .catch((error) => {
          console.log(error);
        });
    },
  },

  computed: {
    ...mapState(["userName"]),
  },
};
</script>

<style lang="scss" scoped>
@import "@/assets/variable.scss";
.login {
  width: 100%;
  height: 100vh;
  display: flex;
  align-items: center;
  justify-content: center;
  background-color: darken($color: #ffffff, $amount: 10);

  &-card {
    width: 50%;
    height: 50%;
    background-color: #ffffff;
    border-radius: 0.8rem;
    box-shadow: 0px 0px 18px -10px rgba(0, 0, 0, 0.56);
    -webkit-box-shadow: 0px 0px 18px -10px rgba(0, 0, 0, 0.56);
    -moz-box-shadow: 0px 0px 18px -10px rgba(0, 0, 0, 0.56);
  }
  &-form {
    width: 80%;
    height: 100%;
    margin: auto;
    display: flex;
    align-items: center;
    flex-direction: column;
    justify-content: center;
    row-gap: 2rem;
    .form-group {
      width: 100%;
    }
    .form-label {
      font-size: 1.4rem;
      line-height: 150%;
      margin-bottom: 1rem;
    }
    .form-control {
      width: 100%;
      height: 4rem;
      padding: 0.4rem 0.8rem;
      border: 0.1rem solid darken($color: #ffffff, $amount: 30);
      &:focus {
        outline: none;
      }
    }
    .btn {
      width: 13rem;
      height: 4rem;
    }
  }

}  
@media only screen and (max-width: 768px) {
    .login {
      padding: 0 2rem !important;
    }
    .login-card {
      width: 100%;
    }
  }
</style>